<template>
  <div class="row mt-5">
    <div class="col">
      <h2 class="mb-3">( ...(`))</h2>
      <div class="row justify-content-center">
        <div class="col-md-6">
          <form @submit.prevent="updatePost">
            <ckeditor
              :editor="editor"
              v-model="editorData"
              :config="editorConfig"
            ></ckeditor>
            <div class="text-right">
              <button
                type="submit"
                class="btn btn-secondary mt-3"
              ></button>
            </div>
          </form>
        </div>
      </div>

      <h6 class="mt-5"></h6>
      <ol>
        <li></li>
        <li></li>
      </ol>
    </div>
  </div>
</template>


<style lang="scss">
.ck-editor__editable {
  min-height: 250px;
}
</style>

<script>
// import ClassicEditor from '../../node_modules/@ckeditor/ckeditor5-build-classic';
import ClassicEditor from '../../../node_modules/@ckeditor/ckeditor5-build-classic';

export default {
  data() {
    return {
      editor: ClassicEditor,
      editorData: '<p></p>',
      editorConfig: {
        placeholder: ' ...',
        // 
        initialData: '<h2></h2>',
        // 
        heading: {
          //  Heading 
          options: [
            {
              model: 'paragraph',
              title: 'Paragraph',
              class: 'ck-heading_paragraph',
            },
            {
              model: 'heading1',
              view: 'h2',
              title: 'Heading 1',
              class: 'ck-heading_heading1',
            },
            {
              model: 'heading2',
              view: 'h3',
              title: 'Heading 2',
              class: 'ck-heading_heading2',
            },
          ],
        },
        language: 'zh',
        // 
      },
    };
  },
  methods: {
    updatePost() {
      const vm = this;
      firebase
        .database()
        .ref()
        .push(vm.editorData);

      vm.$router.push('/admin/uploadPosts-result');
    },
  },
};
</script>
